import React from 'react'
import { useState } from 'react'
import './Header.css'
import logo from '../assets/risousslogo.png'
import iconpepper from '../assets/product/icons/pepper.png'
import icontomato from '../assets/product/icons/tomato.png'
import iconzucchini from '../assets/product/icons/zucchini.png'
import iconeggplant from '../assets/product/icons/eggplant.png'
import iconbean from '../assets/product/icons/beans.png'
import { NavLink } from 'react-router-dom'
import { ReactComponent as Hamburger } from '../assets/burger-menu-svgrepo-com.svg'
import { AiFillCaretDown } from "react-icons/ai";
const Header = () => {
 
  const [showNavbar, setShowNavbar] = useState(false)

  const handleShowNavbar = () => {
    setShowNavbar(!showNavbar)
  }

  return (
    <nav className="navbar  posi">
      <div className="container">
        <div className="logo">
      
          <NavLink to="/">  <img src={logo} alt="Logo" /></NavLink>
        </div>
        <div className="menu-icon" onClick={handleShowNavbar}>
          <Hamburger />
        </div>
        <div className={`nav-elements  ${showNavbar && 'active'}`}>
          <ul>
            <li>
              <NavLink to="/">Home</NavLink>
            </li>
            <li>
       
              <div className="dropdown">
        <button className="dropbtn">Products<AiFillCaretDown/></button>
       <div className="dropdown-content">
         <NavLink to="/pepper">Pepper <img src={iconpepper} alt='iconpepper' className='imgicon'/>  </NavLink>
         <NavLink to="/tomato">Tomato  <img src={icontomato} alt='iconpepper' className='imgicon'/></NavLink>
       <NavLink to="/zucchini">Zucchini  <img src={iconzucchini} alt='iconpepper' className='imgicon'/></NavLink>
       <NavLink to="/eggplant">Eggplant <img src={iconeggplant} alt='iconpepper' className='imgicon'/></NavLink>
       <NavLink to="/bean">Bean<img src={iconbean} alt='iconpepper' className='imgicon'/></NavLink>
         </div>
       </div>
            </li>
            <li>
            <div className="dropdown">
        <button className="dropbtn">Groups <AiFillCaretDown/></button>
       <div className="dropdown-content">
         <NavLink to="/Risouss1">Risouss1</NavLink>
         <NavLink to="/Risouss2">Risouss2</NavLink>
       <NavLink to="/Risouss3">Risouss3</NavLink>
         </div>
       </div>
              
            </li>
            <li>
              <NavLink to="/contact">Contact</NavLink>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  )
}

export default Header