import React from 'react'
import './Home.css'
import im1 from '../assets/home/im1.jpg'
import { NavLink } from 'react-router-dom'
const Home = () => {

  return (
    <div className="">

  <div className='divbg'>
      <div className='divtext'>
      Discover the pure, wholesome flavors of our natural<br/> chemical-free agricultural products for a healthier<br/> happier life
      </div>
    </div>
<div className='div2'>
  <h1>100% natural
  </h1>
  </div>
  <div className='div3'>
<span> Welcome to our world of natural, chemical-free agricultural products! 
 We're committed to providing you with the finest selection of fresh, sustainable, 
 and eco-friendly produce for a healthier lifestyle. <br/>Explore our diverse range of farm-fresh fruits,
  vegetables, and more, all grown with care and respect for the environment. <br/>Join us on this journey towards a greener, 
  more nourishing future.</span>
 <div className='div4'>
  <img src={im1} alt='im1' className='img1'/></div>
  </div>
<div>
<div className="company-info">
  <h2>Risouss Agricole SARL</h2>

  <p>Risouss Agricole is a company specialized in the production and export of fruits and vegetables, particularly in the organic production of red pepper, zucchini, beans, and hot chili pepper, on an area of 113 hectares. With over 12 years of experience in serving its customers and thanks to the vertical integration of its production, packaging, and distribution processes, with a tonnage of 7,500 tons per year, Risouss Agricole is able to deliver its products directly to its customers and meet their requirements for freshness, quality, food safety, and delivery regularity. All its products are certified GLOBALGAP V-5.4, GRASP, COC, BRC V-8, and IFSV-7.</p>

  <p>To achieve this, our company has established an ultra-modern packaging station with a covered area of 10,000 square meters, capable of processing more than 20,000 to 30,000 tons per year of fruits and vegetables. Through on-site platforms, it is able to meet its customers' needs and fully satisfy their requirements in terms of responsiveness, quality, traceability, and food safety of its products.</p>

  <div className="garden-button">
  <NavLink to="/contact">Connect with us</NavLink>
</div>

</div>


</div>

 </div>
  )
}

export default Home